<template>
  <b-navbar-nav>
    <b-nav-item
      v-for="(item,index) in items"
      :key="index"
      :to="item.href"
    >{{item.name}}</b-nav-item>
  </b-navbar-nav>
</template>

<script>
export default {
  data: function() {
    return {
      items: [
        { name: "Home", href: "/" },
        { name: "Browse", href: "/browser" },
        { name: "Search", href: "/search" }
      ],
      activeItem: "Home"
    };
  },
  computed: {
    isActive: () => {
      let isActive = new Array();
      this.items.forEach(element => {
        isActive.push(element.name == this.activeItem);
      });
      return isActive;
    }
  },
  watch: {
    activeItem: () => {
      this.isActive = new Array();
      this.items.forEach(element => {
        this.isActive.push(element.name == this.activeItem);
      });
    }
  },
  methods: {
    changeActive: newVal => {
      this.activeItem = newVal;
    }
  }
};
</script>

