<template>
  <div class="py-5">
    <div class="container">
      <b-card-group columns>
        <b-card
          :title="image.title"
          :img-src="image.path"
          img-top
          tag="article"
          class="mb-4 shadow-sm"
          align-self="stretch"
          v-for="(image,index) in popularImages"
          :key="index"
        >
          <b-card-text class="brief">{{image.description}}</b-card-text>
          <b-button-group>
            <b-button variant="outline-primary" :to="'/detail/'+image.id">View</b-button>
            <b-button variant="outline-success">Like</b-button>
          </b-button-group>
        </b-card>
      </b-card-group>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      popularImages: {}
    };
  },
  created() {
    this.getPopularImage();
  },
  methods: {
    getPopularImage() {
      axios
        .get("../api/GetPopularImage.php")
        .then(response => {
          this.popularImages = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>

<style>
.brief {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
</style>