import Vue from "vue";
import Vuex from 'vuex';
import axios from 'axios';
Vue.use(Vuex);
export default new Vuex.Store({
    state: {
        login: false,
        id: "",
        email: "",
        name: "",
        favor: [],
        image: []
    },
    getters: {
        isLogin: state => { return state.login; }
    },
    mutations: {
        login(state, form) {
            axios
                .post("./api/Login.php", { email: form.email, password: form.password })
                .then(response => {
                    let $receive = response.data;
                    if ($receive.actionState) {
                        console.log($receive.error);
                    } else {
                        for (const prop in state) {
                            if ($receive.hasOwnProperty(prop)) {
                                this.state[prop] = $receive[prop];
                            }
                        }
                        state.login = true;
                    }
                })
                .catch(error => console.log(error));
        },
        logout(state) {
            axios
                .get("")
                .then(response => {
                    let $receive = response.data;
                    if ($receive.actionState) {
                        for (const prop in state) { this.state[prop] = null; }
                        state.login = false;
                    }
                })
                .catch(error => console.log(error));
        },
        signup(state, form) {
            axios
                .post("", { email: form.email, name: form.name, password: form.password })
                .then(response => {
                    let $receive = response.data;
                    if ($receive.actionState) {
                        this.commit('login', { email: form.email, password: form.password });
                    } else {
                        console.log($receive.error);
                    }
                })
                .catch(error => {
                    console.log(error);
                });
        }
    }
});